import { useState } from "react";
import { Routes, Route } from "react-router-dom";
import Topbar from "./scenes/global/Topbar";
import Sidebar from "./scenes/global/Sidebar";
import Login from "./scenes/login";
import Dashboard from "./scenes/dashboard";
import Users from "./scenes/users";
import Contacts from "./scenes/customers";
import Categories from "./scenes/categories";
import Products from "./scenes/products";
import Sales from "./scenes/sales";
import Reports from "./scenes/reports";
import NewSale from "./scenes/sales/new";
import EditSale from "./scenes/sales/edit";
import SellerReport from './scenes/reports/seller';
import CustomerReport from './scenes/reports/customer';
import ProductReport from './scenes/reports/product';
import { CssBaseline, ThemeProvider } from "@mui/material";
import { ColorModeContext, useMode } from "./theme";
import UserState from './contents/User/UserState';


function App() {
  const [theme, colorMode] = useMode();
  const [isSidebar, setIsSidebar] = useState(true);

  return (
    <UserState>
      <ColorModeContext.Provider value={colorMode}>
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <div className="app">
            <Sidebar isSidebar={isSidebar} />
            <main className="content">
              <Topbar setIsSidebar={setIsSidebar} />
              <Routes>
                <Route path="/" element={<Login />} />
                <Route path="/dashboard" element={<Dashboard />} />
                <Route path="/usuarios" element={<Users />} />
                <Route path="/clientes" element={<Contacts />} />
                <Route path="/categorias" element={<Categories />} />
                <Route path="/productos" element={<Products />} />
                <Route path="/ventas" element={<Sales />} />
                <Route path="/ventas/nuevo" element={<NewSale />} />
                <Route path="/ventas/editar/:id" element={<EditSale />} />
                <Route path="/reportes" element={<Reports />} />
                <Route path="/reportes/clientes" element={<CustomerReport />} />
                <Route path="/reportes/vendedor" element={<SellerReport />} />
                <Route path="/reportes/producto" element={<ProductReport />} />
              </Routes>
            </main>
          </div>
        </ThemeProvider>
      </ColorModeContext.Provider>
    </UserState>
  );
}

export default App;
