import { Box, useTheme } from "@mui/material";
import { tokens } from "../../theme";
import EmailIcon from "@mui/icons-material/Email";
import PointOfSaleIcon from "@mui/icons-material/PointOfSale";
import PersonAddIcon from "@mui/icons-material/PersonAdd";
import Header from "../../components/Header";
import StatBox from "../../components/StatBox";
import { useEffect } from "react";
import { useState } from "react";
import Axios from "axios";
import { useNavigate } from "react-router-dom";
import { URL } from '../../data/functions';

const Dashboard = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const navigate = useNavigate();

  const [empleados, setEmpleados] = useState(0);
  const [productos, setProductos] = useState(0);
  const [separados, setSeparados] = useState(0);
  const [ventas, setVentas] = useState(0);

  const getTotals = async () => {
    const users = await Axios({
      url: URL+"/api/user/total",
      method: 'GET',
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    });

    const products = await Axios({
      url: URL+"/api/product/total",
      method: 'GET',
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    });

    const ventas = await Axios({
      url: URL+"/api/sale/today",
      method: 'GET',
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    });

    setEmpleados(users.data.total);
    setProductos(products.data.total);
    setSeparados(ventas.data.separado);
    setVentas(ventas.data.ventas)
  }

  useEffect(()=>{
    getTotals()
  }, [])

  return (
    <Box m="20px">
      {/* HEADER */}
      <Box display="flex" justifyContent="space-between" alignItems="center">
        <Header title="DASHBOARD" subtitle="Bienvenido al panel principal" />
      </Box>

      {/* GRID */}
      <Box
        display="grid"
        gridTemplateColumns="repeat(9, 1fr)"
        gridAutoRows="140px"
        gap="20px"
      >
        {/* ROW 1 */}
        <Box
          gridColumn="span 3"
          backgroundColor={colors.primary[400]}
          display="flex"
          alignItems="center"
          justifyContent="center"
          sx={{ cursor: 'pointer' }}
          onClick={()=>navigate('/ventas')}
        >
          <StatBox
            title={ventas}
            subtitle="Ventas de Hoy"
            icon={
              <PointOfSaleIcon
                sx={{ color: colors.greenAccent[600], fontSize: "26px" }}
              />
            }
          />
        </Box>
        <Box
          gridColumn="span 3"
          backgroundColor={colors.primary[400]}
          display="flex"
          alignItems="center"
          justifyContent="center"
          sx={{ cursor: 'pointer' }}
          onClick={()=>navigate('/ventas')}
        >
          <StatBox
            title={separados}
            subtitle="Separados del Da"
            icon={
              <PointOfSaleIcon
                sx={{ color: colors.greenAccent[600], fontSize: "26px" }}
              />
            }
          />
        </Box>
        <Box
          gridColumn="span 3"
          backgroundColor={colors.primary[400]}
          display="flex"
          alignItems="center"
          justifyContent="center"
          sx={{ cursor: 'pointer' }}
          onClick={()=>navigate('/usuarios')}
        >
          <StatBox
            title={empleados}
            subtitle="Empleados"
            icon={
              <PersonAddIcon
                sx={{ color: colors.greenAccent[600], fontSize: "26px" }}
              />
            }
          />
        </Box>       
      </Box>
      {/* =========== */}
      <Box
        display="grid"
        gridAutoRows="140px"
        onClick={()=>navigate('/productos')}
        sx={{
          cursor: 'pointer',
          marginTop: "18px",
        }}
      >
        <Box
          backgroundColor={colors.primary[400]}
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <StatBox
            title={productos}
            subtitle="Productos"
            icon={
              <EmailIcon
                sx={{ color: colors.greenAccent[600], fontSize: "26px" }}
              />
            }
          />
        </Box>
      </Box>
    </Box>
  );
};

export default Dashboard;
