import { Box, Button, TextField, FormControl, InputLabel, Select, MenuItem, Snackbar, Alert } from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import Header from "../../components/Header";

import { useState, useEffect } from "react";
import Axios from "axios";
import useMediaQuery from "@mui/material/useMediaQuery";
import { formatDate } from '../../data/functions';
import { useParams } from 'react-router-dom'

import DeleteIcon from '@mui/icons-material/Delete';
import { URL } from '../../data/functions';

const NewSale = () => {
  const { id } = useParams();
  const [cart, setCart] = useState([]);  
  const [customers, setCustomers] = useState([]);  
  const [showAlert, setShowAlert] = useState(false);
  const [messageInfo, setMessageInfo] = useState({});

  const [cliente, setCliente] = useState('');
  const [codigoPdto, setCodigoPdto] = useState('');

  const [tipoTransaccion, setTipoTransaccion] = useState('');

  const [total, setTotal] = useState(0);
  const [pagado, setPagado] = useState(0);
  const [fecha, setFecha] = useState('');
  const [fechaPago, setFechaPago] = useState('');
  const [observacion, setObservacion] = useState('');

  const isNonMobile = useMediaQuery("(min-width:600px)");
 
  const getSale = () => {
    Axios({
        url: URL + "/api/sale/"+id,
        method: 'GET',
        headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
        setCliente(data.id_customer);
        setPagado(data.paid);
        setObservacion(data.observation);
        setTipoTransaccion(data.type);
        setFecha(formatDate(data.created_at))
        setFechaPago(formatDate(data.pay_date))
    })
  }

  const getSaleDetails = () => {
    Axios({
        url: URL + "/api/sale/details/"+id,
        method: 'GET',
        headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
        setCart(data);
        setTotal(getTotal(data));
    })
  }

  const getCustomers = () => {
    Axios({
      url: URL + "/api/customer",
      method: 'POST',
      data: { perPage: 200 },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setCustomers(data.data);
    })
  }

  const getTotal = (cart) => {
    let sum = 0;
    cart.forEach(element => {
      sum += (element.selling_price * element.amount);
    });
    return sum;
  }

  const buscarProducto = () => {
    Axios({
      url: URL + "/api/product/search",
      method: 'POST',
      data: { code: codigoPdto },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      if(!data.id) return setCodigoPdto('');;
      const index = cart.findIndex(product => product.id_product === data.id);
      if(index === -1){
        Axios({
            url: URL + "/api/sale/detail",
            method: 'POST',
            data: { 
                id_sale: id,
                id_product: data.id,
                amount: 1 
            },
            headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
        }).then((res)=>{
            setCart([...cart, {...data, id_sd: res.data.data.id, name_product: data.name, amount: 1}]);
            setTotal(getTotal([...cart, {...data, amount: 1}]));
        })
      } else {
        let newCart = [].concat(cart);
        newCart[index].amount++;
        setCart(newCart);
        setTotal(getTotal(newCart));
      }
      setCodigoPdto('');
    })
  }

  const removeCartItem = (id) => {
    Axios({
        url: URL + "/api/sale/detail/delete/"+id,
        method: 'POST',
        headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
        getSaleDetails()
    })
  }

  const changeAmount = ({id, value}) => {
    if(!value) return;
    Axios({
        url: URL + "/api/sale/detail/update/"+id,
        method: 'POST',
        data: { amount: value },
        headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(() => {
        getSaleDetails();
    }).catch(e=> {
        if(e.response) {
            setShowAlert(true);
            setMessageInfo({icon:'error', message: e.response.data.message});
        }
        else console.log(e);
    })
  }

  const validaPagado = (value) => {
    if(value > total) setPagado(total);
    else if(value < 0) setPagado(0);
    else setPagado(value);
  }

  const handleFormSubmit = () => {
    let values = {
      type: tipoTransaccion,
      observation: observacion,
      paid: pagado,
      pay_date: fechaPago
    }
    Axios({
      url: URL + "/api/sale/update/"+id,
      method: 'POST',
      data: values,
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
        setShowAlert(true);
        setMessageInfo({icon:'success', message: data.message});
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

    useEffect(()=>{
        function loadData (){
            getCustomers();
            getSale();
            getSaleDetails();
        }
        loadData();
    }, [])

  const columns = [
    { field: "name_product", headerName: "Producto", flex: 1 },
    { field: "measuring_unit", headerName: "UM", flex: 1 },
    {
      field: "amount",
      renderCell: ({row})=>{
        return <Box>
          <input 
            style={{
              width: '100%',
              display: 'block',
              textAlign: 'center',
              padding: '.1rem 0'
            }}
            type="number" 
            value={ row.amount }
            onChange={(e)=>changeAmount({id:row.id_sd, value: e.target.value}) }
          />
        </Box>
      }, headerName: 'Cantidad'
    },
    { field: "selling_price", headerName: "Precio Venta", flex: 1 },
    { field: "stock", headerName: "Stock", flex: 1 },
    {
      field: "subtotal",
      renderCell: ({row})=>{
        return <Box>
          { row.amount * row.selling_price }
        </Box>
      }, headerName: 'Subtotal'
    },
    {
      field: "opciones",
      renderCell: ({row})=>{
        return <Box>
          <Button
            variant="contained"
            size="small"
            color="error"
            onClick={()=>removeCartItem(row.id_sd)}
          >
            <DeleteIcon/>
          </Button>
        </Box>
      }, headerName: 'Opciones'
    }
    
    
  ]

  return (
    <Box m="20px">
      <Header
        title="ACTUALIZAR VENTA"
      />
      <Box
        m="40px 0 0 0"
      >
        <Box
          display="grid"
          gap="30px"
          gridTemplateColumns="repeat(12, minmax(0, 1fr))"
          sx={{
            "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
          }}
        >
          
          <FormControl sx={{ gridColumn: "span 6" }} variant="filled">
            <InputLabel>Cliente</InputLabel>
            <Select
              name="id_customer"
              value={cliente}
              onChange={(e)=>setCliente(e.target.value)}
            >
              {
                customers.map(customer => (
                  <MenuItem 
                    key={customer.id + customer.fullname} 
                    value={customer.id}
                  >{customer.fullname} - {customer.document}</MenuItem>
                ))
              }
            </Select>
          </FormControl>
          <TextField
            fullWidth
            variant="filled"
            type="date"
            label="Fecha"
            value={fecha}
            disabled={true}
            sx={{ gridColumn: "span 3" }}
            InputLabelProps={{shrink: true}}
          />
          <TextField
            fullWidth
            variant="filled"
            type="number"
            label="Nmero"
            value={id}
            disabled={true}
            sx={{ gridColumn: "span 3" }}
            InputLabelProps={{shrink: true}}
          />


          <Box sx={{ gridColumn: "span 4" }}>
            <form 
              onSubmit={e => {
                e.preventDefault();
                buscarProducto();
              }}
            >
              <TextField
                fullWidth
                variant="filled"
                type="text"
                label="Cdigo de Barras"
                name="codigo"
                value={codigoPdto}
                onChange={e=>setCodigoPdto(e.target.value)}
                InputLabelProps={{shrink: true}}
              />
            </form>
          </Box>
          
          <Box 
            sx={{ gridColumn: "span 12" }}
            height={ ((cart.length + 1) * 3.35) + "rem" }
          >
            <DataGrid
              rows={cart}
              getRowId={(row)=>row.id_sd+cart.length}
              columns={columns}
              hideFooter={true}
            />
          </Box>
          <Box
            sx={{ gridColumn: "span 12" }}
            style={{
              textAlign: "right",
              fontSize: '1.5rem',
              padding: '.5rem 0'
            }}
          >
            Total: <span>${total}</span>
          </Box>
          <FormControl sx={{ gridColumn: "span 3" }} variant="filled">
            <InputLabel>Tipo de transaccin</InputLabel>
            <Select 
              value={tipoTransaccion}
              onChange={(e)=>setTipoTransaccion(e.target.value)}
              name="tipoTransaccion"
            >
                <MenuItem value="venta">Venta</MenuItem>
                <MenuItem value="separado">Separado</MenuItem>
            </Select>
          </FormControl>

          <TextField
            fullWidth
            variant="filled"
            type="date"
            label="Fecha de Pago"
            value={fechaPago}
            onChange={e=>setFechaPago(e.target.value)}
            InputLabelProps={{shrink: true}}
            sx={{ gridColumn: "span 3" }}
          />
          <TextField
            fullWidth
            variant="filled"
            type="number"
            label="Monto de Deuda"
            disabled={true}
            InputLabelProps={{shrink: true}}
            sx={{ gridColumn: "span 3" }}
            value={total}
          />
          {(tipoTransaccion === "separado")?(
            <TextField
              fullWidth
              variant="filled"
              type="number"
              label="Monto Pagado"
              value={pagado}
              onChange={(e)=>validaPagado(e.target.value)}
              InputLabelProps={{shrink: true}}
              sx={{ gridColumn: "span 3" }}
            />
          ):""}
          <TextField
            fullWidth
            placeholder="Observaciones"
            label="Observaciones"
            multiline
            rows={2}
            value={observacion}
            onChange={(e)=>setObservacion(e.target.value)}
            sx={{ gridColumn: "span 12" }}
          />
        </Box>
        <Box display="flex" justifyContent="end" mt="20px">
          <Button 
            type="submit" 
            color="secondary" 
            variant="contained"
            onClick={()=>handleFormSubmit()}
          >
            Guardar
          </Button>
        </Box>
      </Box>


      {/* ALERT */}
      <Snackbar 
        open={showAlert} 
        autoHideDuration={3000}
        onClose={()=>setShowAlert(false)}
        anchorOrigin={{ vertical, horizontal }}
        key={vertical + horizontal}
        TransitionProps={{onExited:()=>{}}}
      >
        <Alert severity={messageInfo.icon} sx={{ width: '100%' }}>
          {messageInfo.message}
        </Alert>
      </Snackbar>
    </Box>
  ); 
};


const vertical = 'top';
const horizontal = "right";


export default NewSale;
