import { GET_PROFILE, SET_PROFILE} from "../types";

// eslint-disable-next-line import/no-anonymous-default-export
export default (state, action) => {
    const {payload, type} = action;

    switch (type) {
        case GET_PROFILE:
           return {
            ...state,
            profile: payload
           } 
        case SET_PROFILE:
            return {
            ...state,
            profile: payload
            }
        default: 
           return state;
    }
}