import { Box, Button, TextField, Fade, Modal, FormControl, InputLabel, Select, MenuItem, Snackbar, Alert } from "@mui/material";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import { useTheme } from "@mui/material";
import { useState } from "react";
import { useEffect } from "react";
import Axios from "axios";
import { Formik } from "formik";
import * as yup from 'yup';
import useMediaQuery from "@mui/material/useMediaQuery";
import { URL } from '../../data/functions';

import AddIcon from '@mui/icons-material/Add';
import CreateIcon from '@mui/icons-material/Create';

const Customers = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [customers, setCustomers] = useState([]);
  const [rowCount, setRowCount] = useState(0);
  const [curPage, setCurPage] = useState(0);
  const [rowPer, setRowPer] = useState(25);

  

  const [registerModal, setRegisterModal] = useState(false);
  const [updateModal, setUpdateModal] = useState(false);

  const [showAlert, setShowAlert] = useState(false);
  const [messageInfo, setMessageInfo] = useState({});
  const [updateValues, setUpdatevalues] = useState({});

  const searchData = (page, perPage) => {
    let url = URL+'/api/customer';
    if(page) url += `?page=${page+1}`;
    Axios({
      url,
      method: 'POST',
      data: { perPage },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setCustomers(data.data);
      setRowCount(data.total);
    })
  }

  const handleRegisterFormSubmit = (values) => {
    Axios({
      url: URL+"/api/customer/register",
      method: 'POST',
      data: values,
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
      setMessageInfo({icon:'success', message: 'Cliente registrado exitosamente'});
      setShowAlert(true);
      setRegisterModal(false);
      searchData(curPage, rowPer);
      
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

  const handleUpdateFormSubmit = ({id, ...values}) => {
    Axios({
      url: URL+`/api/customer/update/${id}`,
      method: 'POST',
      data: values,
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
      setMessageInfo({icon:'success', message: 'Cliente actualizado exitosamente'});
      setShowAlert(true);
      setUpdateModal(false);
      searchData(curPage, rowPer);
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

  useEffect(()=>{
    searchData(0, 25);
  }, [setCustomers, setRowCount])

  const columns = [
    { field: "id", headerName: "ID", flex: 0.5 },
    { field: "fullname", headerName: "Nombre", flex: 1 },
    {
      field: "doc_type",
      headerName: "Documento",
      flex: 1,
    },
    {
      field: "document",
      headerName: "Nmero",
      type: "number",
      headerAlign: "left",
      align: "left",
    },
    {
      field: "phone",
      headerName: "Telfono",
      flex: 1,
    },
    {
      field: "email",
      headerName: "Email",
      flex: 1,
    },
    {
      field: "birth_date",
      headerName: "Fecha Nacimiento",
      flex: 1,
    },
    {
      headerName: 'Accin',
      flex: 1,
      renderCell: ({row})=>{
        return <Box>
          <Button
            variant="contained"
            size="small"
            color="primary"
            onClick={()=>{
              setUpdateModal(true)
              setUpdatevalues(row);
            }}
          >
            <CreateIcon/>
          </Button>
        </Box>
      }
    }
  ];

  const isNonMobile = useMediaQuery("(min-width:600px)");


  return (
    <Box m="20px">
      <Header
        title="CLIENTES"
        subtitle="Gestiona los clientes del sistema"
      />
      <Button 
        type="submit" 
        color="secondary" 
        variant="contained" 
        onClick={()=>setRegisterModal(true)}
      > <AddIcon/> NUEVO 
      </Button>
      <Box
        m="40px 0 0 0"
        height="75vh"
        sx={{
          "& .MuiDataGrid-root": {
            border: "none",
          },
          "& .MuiDataGrid-cell": {
            borderBottom: "none",
          },
          "& .name-column--cell": {
            color: colors.greenAccent[300],
          },
          "& .MuiDataGrid-columnHeaders": {
            backgroundColor: colors.blueAccent[700],
            borderBottom: "none",
          },
          "& .MuiDataGrid-virtualScroller": {
            backgroundColor: colors.primary[400],
          },
          "& .MuiDataGrid-footerContainer": {
            borderTop: "none",
            backgroundColor: colors.blueAccent[700],
          },
          "& .MuiCheckbox-root": {
            color: `${colors.greenAccent[200]} !important`,
          },
          "& .MuiDataGrid-toolbarContainer .MuiButton-text": {
            color: `${colors.grey[100]} !important`,
          },
        }}
      >
        <DataGrid
          rows={customers}
          columns={columns}
          rowCount={rowCount}
          components={{ Toolbar: GridToolbar }}
          onPageChange={(page)=>{
            setCurPage(page);
            searchData(page, rowPer)
          }}
          onPageSizeChange={(pageSize)=>{
            setRowPer(pageSize);
            searchData(curPage, pageSize);
          }}
          initialState={{
            pagination: {
              pageSize: 25,
            },
          }}
        />
      </Box>

      <Modal
        open={registerModal}
        onClose={()=>setRegisterModal(false)}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Fade in={registerModal}>
          <Box sx={style}>
            <h2 id="parent-modal-title">Registro de Cliente</h2>
            <Formik
              onSubmit={handleRegisterFormSubmit}
              initialValues={initialValues}
              validationSchema={checkoutData}
            >
              {({
                values,
                errors,
                touched,
                handleBlur,
                handleChange,
                handleSubmit
              }) => (
                <form onSubmit={handleSubmit}>
                  <Box
                    display="grid"
                    gap="30px"
                    gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                    sx={{
                      "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                    }}
                  >
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nombre"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.fullname}
                      name="fullname"
                      error={!!touched.fullname && !!errors.fullname}
                      helperText={touched.fullname && errors.fullname}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <FormControl sx={{ gridColumn: "span 2" }} variant="filled">
                      <InputLabel>Tipo de Documento</InputLabel>
                      <Select
                        name="doc_type"
                        value={values.doc_type}
                        onChange={handleChange}
                      >
                        <MenuItem value="CC">CC</MenuItem>
                        <MenuItem value="TI">TI</MenuItem>
                        <MenuItem value="TI">CE</MenuItem>
                      </Select>
                    </FormControl>
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nmero Documento"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.document}
                      name="document"
                      error={!!touched.document && !!errors.document}
                      helperText={touched.document && errors.document}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Telfono"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phone}
                      name="phone"
                      error={!!touched.phone && !!errors.phone}
                      helperText={touched.phone && errors.phone}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Email"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phone}
                      name="email"
                      error={!!touched.email && !!errors.email}
                      helperText={touched.email && errors.email}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Direccin"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.adress}
                      name="adress"
                      error={!!touched.adress && !!errors.adress}
                      helperText={touched.adress && errors.adress}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="date"
                      label="Fecha de Nacimiento"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.birth_date}
                      name="birth_date"
                      error={!!touched.birth_date && !!errors.birth_date}
                      helperText={touched.birth_date && errors.birth_date}
                      sx={{ gridColumn: "span 4" }}
                      InputLabelProps={{shrink: true}}
                    />
                  </Box>
                  <Box display="flex" justifyContent="end" mt="20px">
                    <Button type="submit" color="secondary" variant="contained">
                      Registrar Cliente
                    </Button>
                  </Box>
                </form>
              )}
            </Formik>
          </Box>
        </Fade>
      </Modal>

      {/* UPDATE MODAL */}
      <Modal
        open={updateModal}
        onClose={()=>setUpdateModal(false)}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Fade in={updateModal}>
          <Box sx={style}>
            <h2 id="parent-modal-title">Actualizacin de Cliente</h2>
            <Formik
              onSubmit={handleUpdateFormSubmit}
              initialValues={updateValues}
              validationSchema={checkoutData}
            >
              {({
                values,
                errors,
                touched,
                handleBlur,
                handleChange,
                handleSubmit
              }) => (
                <form onSubmit={handleSubmit}>
                  <Box
                    display="grid"
                    gap="30px"
                    gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                    sx={{
                      "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                    }}
                  >
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nombre"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.fullname}
                      name="fullname"
                      error={!!touched.fullname && !!errors.fullname}
                      helperText={touched.fullname && errors.fullname}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <FormControl sx={{ gridColumn: "span 2" }} variant="filled">
                      <InputLabel>Tipo de Documento</InputLabel>
                      <Select
                        name="doc_type"
                        value={values.doc_type}
                        onChange={handleChange}
                      >
                        <MenuItem value="CC">CC</MenuItem>
                        <MenuItem value="TI">TI</MenuItem>
                        <MenuItem value="TI">CE</MenuItem>
                      </Select>
                    </FormControl>
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nmero Documento"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.document}
                      name="document"
                      error={!!touched.document && !!errors.document}
                      helperText={touched.document && errors.document}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Telfono"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phone}
                      name="phone"
                      error={!!touched.phone && !!errors.phone}
                      helperText={touched.phone && errors.phone}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Email"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phone}
                      name="email"
                      error={!!touched.email && !!errors.email}
                      helperText={touched.email && errors.email}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Direccin"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.adress}
                      name="adress"
                      error={!!touched.adress && !!errors.adress}
                      helperText={touched.adress && errors.adress}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="date"
                      label="Fecha de Nacimiento"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.birth_date}
                      name="birth_date"
                      error={!!touched.birth_date && !!errors.birth_date}
                      helperText={touched.birth_date && errors.birth_date}
                      sx={{ gridColumn: "span 4" }}
                      InputLabelProps={{shrink: true}}
                    />
                  </Box>
                  <Box display="flex" justifyContent="end" mt="20px">
                    <Button type="submit" color="secondary" variant="contained">
                      Guardar
                    </Button>
                  </Box>
                </form>
              )}
            </Formik>
          </Box>
        </Fade>
      </Modal>

      {/* ALERT */}
      <Snackbar 
        open={showAlert} 
        autoHideDuration={3000}
        onClose={()=>setShowAlert(false)}
        anchorOrigin={{ vertical, horizontal }}
        key={vertical + horizontal}
        TransitionProps={{onExited:()=>{}}}
      >
        <Alert severity={messageInfo.icon} sx={{ width: '100%' }}>
          {messageInfo.message}
        </Alert>
      </Snackbar>
    </Box>
  ); 
};

const checkoutData = yup.object().shape({
  fullname: yup.string().required("Requerido"),
  doc_type: yup.string().required("Requerido"),
  document: yup.string().required("Requerido"),
  birth_date: yup.string().required("Requerido"),
});

const initialValues = {
  fullname: "",
  doc_type: "CC",
  document: "",
  phone: "",
  email: "",
  adress: "",
  birth_date: ""
};

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 560,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const vertical = 'top';
const horizontal = "right";

export default Customers;
