export const URL = '';

export const formatDate = (date) => {
    var fecha = new Date(date);
    var ano = fecha.getFullYear();
    var mes = fecha.getMonth()+1;
    var dia = fecha.getDate();
    if(mes < 10)  mes = "0" + mes
    if(dia < 10) dia = "0" + dia
    return ano+"-"+mes+"-"+dia;
}
