import { Box, Button, Modal, Fade, useTheme, TextField, Snackbar, Alert, FormControl, InputLabel, Select, MenuItem } from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";
import Header from "../../components/Header";
import { DataGrid } from "@mui/x-data-grid";
import { useEffect, useState } from "react";
import { tokens } from "../../theme";
import { Formik } from "formik";
import * as yup from 'yup';
import Axios from 'axios';
import { URL } from "../../data/functions";


import AddIcon from '@mui/icons-material/Add';
import CreateIcon from '@mui/icons-material/Create';

const Users = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [users, setUsers] = useState([]);
  const [rowCount, setRowCount] = useState(0);
  const [curPage, setCurPage] = useState(0);
  const [rowPer, setRowPer] = useState(25);

  const [registerModal, setRegisterModal] = useState(false);
  const [updateModal, setUpdateModal] = useState(false);

  const [showAlert, setShowAlert] = useState(false);
  const [messageInfo, setMessageInfo] = useState({});
  const [updateValues, setUpdatevalues] = useState({});


  const searchUsers = (page, perPage) => {
    let url = URL+'/api/user';
    if(page) url += `?page=${page+1}`;
    Axios({
      url,
      method: 'POST',
      data: { perPage },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setUsers(data.data);
      setRowCount(data.total);
    })
  }

  const handleRegisterFormSubmit = ({fullname, login, password}) => {
    Axios({
      url: URL + "/api/user/register",
      method: 'POST',
      data: {fullname, login, password, id_store: 1},
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
      setMessageInfo({icon:'success', message: 'Usuario registrado exitosamente'});
      setShowAlert(true);
      setRegisterModal(false);
      searchUsers(curPage, rowPer);
      
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  };

  const handleUpdateFormSubmit = ({id, fullname, login, state, id_store}) => {
    Axios({
      url: URL + `/api/user/update/${id}`,
      method: 'POST',
      data: {fullname, login, state, id_store},
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
      setMessageInfo({icon:'success', message: 'Usuario actualizado exitosamente'});
      setShowAlert(true);
      setUpdateModal(false);
      searchUsers(curPage, rowPer);
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

  useEffect(()=>{
    searchUsers(0, 25);
  }, [setUsers, setRowCount])

  const columns = [
    {
      field: "id",
      headerName: "ID",
    },
    {
      field: "fullname",
      headerName: "Nombre",
      flex: 1,
      cellClassName: "name-column--cell",
    },
    {
      field: "login",
      headerName: "Login",
      flex: 1,
    },
    {
      field: "role",
      headerName: "Rol",
      flex: 1,
    },
    {
      field: "state",
      headerName: "Estado",
      flex: 1,
    },
    {
      headerName: 'Accin',
      flex: 1,
      renderCell: ({row})=>{
        return <Box>
          <Button
            variant="contained"
            size="small"
            color="primary"
            onClick={()=>{
              setUpdateModal(true)
              setUpdatevalues(row);
            }}
          >
            <CreateIcon/>
          </Button>
        </Box>
      }
    }
  ];

  const isNonMobile = useMediaQuery("(min-width:600px)");

  return (
    <Box m="20px">
      <Header title="USUARIOS" subtitle="Gestiona los encargados del sistema" />
      {/* REGISTER MODAL */}
      <Button 
        type="submit" 
        color="secondary" 
        variant="contained" 
        onClick={()=>setRegisterModal(true)}
      > <AddIcon/> NUEVO 
      </Button>

      <Modal
        open={registerModal}
        onClose={()=>setRegisterModal(false)}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Fade in={registerModal}>
          <Box sx={style}>
            <h2 id="parent-modal-title">Registro de Usuario</h2>
            <Formik
              onSubmit={handleRegisterFormSubmit}
              initialValues={initialValues}
              validationSchema={checkoutRegister}
            >
              {({
                values,
                errors,
                touched,
                handleBlur,
                handleChange,
                handleSubmit
              }) => (
                <form onSubmit={handleSubmit}>
                  <Box
                    display="grid"
                    gap="30px"
                    gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                    sx={{
                      "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                    }}
                  >
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nombre Completo"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.fullname}
                      name="fullname"
                      error={!!touched.fullname && !!errors.fullname}
                      helperText={touched.fullname && errors.fullname}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Login"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.login}
                      name="login"
                      error={!!touched.login && !!errors.login}
                      helperText={touched.login && errors.login}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="password"
                      label="Contrasea"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.password}
                      name="password"
                      error={!!touched.password && !!errors.password}
                      helperText={touched.password && errors.password}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="password"
                      label="Confirmar Contrasea"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.confirmPassword}
                      name="confirmPassword"
                      error={!!touched.confirmPassword && !!errors.confirmPassword}
                      helperText={touched.confirmPassword && errors.confirmPassword}
                      sx={{ gridColumn: "span 2" }}
                    />
                  </Box>
                  <Box display="flex" justifyContent="end" mt="20px">
                    <Button type="submit" color="secondary" variant="contained">
                      Registrar Usuario
                    </Button>
                  </Box>
                </form>
              )}
            </Formik>
          </Box>
        </Fade>
      </Modal>



      {/* UPDATE MODAL */}
      <Modal
        open={updateModal}
        onClose={()=>setUpdateModal(false)}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Fade in={updateModal}>
          <Box sx={style}>
            <h2 id="parent-modal-title">Actualizacin de Usuario</h2>
            <Formik
              onSubmit={handleUpdateFormSubmit}
              initialValues={updateValues}
              validationSchema={checkoutUpdate}
            >
              {({
                values,
                errors,
                touched,
                handleBlur,
                handleChange,
                handleSubmit
              }) => (
                <form onSubmit={handleSubmit}>
                  <Box
                    display="grid"
                    gap="30px"
                    gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                    sx={{
                      "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                    }}
                  >
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nombre Completo"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.fullname}
                      name="fullname"
                      error={!!touched.fullname && !!errors.fullname}
                      helperText={touched.fullname && errors.fullname}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Login"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.login}
                      name="login"
                      error={!!touched.login && !!errors.login}
                      helperText={touched.login && errors.login}
                      sx={{ gridColumn: "span 4" }}
                    />

                    <FormControl sx={{ gridColumn: "span 4" }} variant="standard">
                      <InputLabel id="demo-customized-select-label">Estado</InputLabel>
                      <Select
                        name="state"
                        value={values.state}
                        onChange={handleChange}
                      >
                        <MenuItem value="activo">Activo</MenuItem>
                        <MenuItem value="inactivo">Inactivo</MenuItem>
                      </Select>
                    </FormControl>


                  </Box>
                  <Box display="flex" justifyContent="end" mt="20px">
                    <Button type="submit" color="secondary" variant="contained">
                      Guardar
                    </Button>
                  </Box>
                </form>
              )}
            </Formik>
          </Box>
        </Fade>
      </Modal>

      {/* TABLE */}
      <Box
        m="40px 0 0 0"
        height="60vh"
        sx={{
          "& .MuiDataGrid-root": {
            border: "none",
          },
          "& .MuiDataGrid-cell": {
            borderBottom: "none",
          },
          "& .name-column--cell": {
            color: colors.greenAccent[300],
          },
          "& .MuiDataGrid-columnHeaders": {
            backgroundColor: colors.blueAccent[700],
            borderBottom: "none",
          },
          "& .MuiDataGrid-virtualScroller": {
            backgroundColor: colors.primary[400],
          },
          "& .MuiDataGrid-footerContainer": {
            borderTop: "none",
            backgroundColor: colors.blueAccent[700],
          },
          "& .MuiCheckbox-root": {
            color: `${colors.greenAccent[200]} !important`,
          },
        }}
      >
        <DataGrid 
          rows={users} 
          columns={columns}
          rowCount={rowCount}
          getRowId={(row)=>(row.id+users.length)}
          onPageChange={(page)=>{
            setCurPage(page);
            searchUsers(page, rowPer)
          }}
          onPageSizeChange={(pageSize)=>{
            setRowPer(pageSize);
            searchUsers(curPage, pageSize);
          }}
          initialState={{
            pagination: {
              pageSize: 25,
            },
          }}
        />
      </Box>

      {/* ALERT */}
      <Snackbar 
        open={showAlert} 
        autoHideDuration={3000}
        onClose={()=>setShowAlert(false)}
        anchorOrigin={{ vertical, horizontal }}
        key={vertical + horizontal}
        TransitionProps={{onExited:()=>{}}}
      >
        <Alert severity={messageInfo.icon} sx={{ width: '100%' }}>
          {messageInfo.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

const checkoutRegister = yup.object().shape({
  fullname: yup.string().required("Requerido"),
  login: yup.string().required("Requerido"),
  password: yup.string().required("Requerido"),
  confirmPassword: yup.string()
    .oneOf([yup.ref('password'), null], 'Las contraseas no coinciden')
});

const checkoutUpdate = yup.object().shape({
  fullname: yup.string().required("Requerido"),
  login: yup.string().required("Requerido"),
});

const initialValues = {
  fullname: "",
  login: "",
  password: "",
  confirmPassword: "",
};

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 560,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const vertical = 'top';
const horizontal = "right";

export default Users;
