import { Box, Button, TextField, FormControl, InputLabel, Select, MenuItem } from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import { useTheme } from "@mui/material";
import { useState } from "react";
import { useEffect } from "react";
import Axios from "axios";
import useMediaQuery from "@mui/material/useMediaQuery";
import { formatDate } from '../../data/functions';

const Customer = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [sales, setSales] = useState([]);

  const [fechaInicio, setFechaInicio] = useState('')
  const [fechaFin, setFechaFin] = useState('')
  const [cliente, setCliente] = useState('');
  const [customers, setCustomers] = useState([]);

  const searchData = () => {
    let url = URL+'/api/report/customer';
    Axios({
      url,
      method: 'POST',
      data: { id_customer: cliente, initial_date: fechaInicio, finish_date: fechaFin },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setSales(data);
    })
  }

  const getCustomers = () => {
    Axios({
      url: URL+"/api/customer",
      method: 'POST',
      data: { perPage: 200 },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setCliente(data.data[0].id)
      setCustomers(data.data);
    })
  }


  useEffect(()=>{
    function listarDatos (){
      getCustomers();
      searchData();
      setFechaInicio(formatDate(new Date()))
      setFechaFin(formatDate(new Date()))
    }
    listarDatos();
  }, [])

  const columns = [
    { field: "id_sale", headerName: "N", flex: 0.5 },
    { field: "created_at", headerName: "Fecha", flex: 1 },
    { field: "customer_name", headerName: "Cliente", flex: 1 },
    { field: "customer_doc", headerName: "Documento", flex: 0.7, },
    { field: "customer_num", headerName: "Nmero", type: "number", headerAlign: "left", align: "left",},
    { field: "employed", headerName: "Vendedor", flex: 1,},
    { field: "total_selling", headerName: "Total Venta", flex: 1,},
    { field: "type", headerName: "Tipo de Pago", flex: 1,},
    { 
      field: "paid",
      headerName: "Estado", 
      renderCell: ({row})=>{
        return <Box>{
            (row.total_selling === row.paid)
            ?"Facturado"
            :"Pendiente"
          }
        </Box>
      }
  }
  ];

  const isNonMobile = useMediaQuery("(min-width:600px)");


  return (
    <Box m="20px">
      <Header
        title="REPORTES"
        subtitle="Consulta de ventas por fecha y cliente"
      />
      <Box
        m="40px 0 0 0"
      >
        <Box
          display="grid"
          gap="30px"
          gridTemplateColumns="repeat(12, minmax(0, 1fr))"
          sx={{
            "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
          }}
        >
          <TextField
            fullWidth
            variant="filled"
            type="date"
            label="Fecha Inicio"
            value={fechaInicio}
            onChange={(e)=>setFechaInicio(e.target.value)}
            sx={{ gridColumn: "span 3" }}
            InputLabelProps={{shrink: true}}
          />
          <TextField
            fullWidth
            variant="filled"
            type="date"
            label="Fecha Fin"
            value={fechaFin}
            onChange={(e)=>setFechaFin(e.target.value)}
            sx={{ gridColumn: "span 3" }}
            InputLabelProps={{shrink: true}}
          />
          <Box sx={{ gridColumn: "span 1" }}></Box>
          <FormControl sx={{ gridColumn: "span 3" }} variant="filled">
            <InputLabel>Cliente</InputLabel>
            <Select
              name="id_customer"
              value={cliente}
              onChange={(e)=>setCliente(e.target.value)}
            >
              {
                customers.map(customer => (
                  <MenuItem 
                    key={customer.id + customer.fullname} 
                    value={customer.id}
                  >{customer.fullname} - {customer.document}</MenuItem>
                ))
              }
            </Select>
          </FormControl>
          <Button
            fullWidth
            variant="contained"
            size="small"
            color="secondary"
            sx={{ gridColumn: "span 2" }}
            onClick={()=>searchData()}
          >
            Buscar
          </Button>
        </Box>
      </Box>
      <Box
        m="40px 0 0 0"
        height="60vh"
        sx={{
          "& .MuiDataGrid-root": {
            border: "none",
          },
          "& .MuiDataGrid-cell": {
            borderBottom: "none",
          },
          "& .name-column--cell": {
            color: colors.greenAccent[300],
          },
          "& .MuiDataGrid-columnHeaders": {
            backgroundColor: colors.blueAccent[700],
            borderBottom: "none",
          },
          "& .MuiDataGrid-virtualScroller": {
            backgroundColor: colors.primary[400],
          },
          "& .MuiDataGrid-footerContainer": {
            borderTop: "none",
            backgroundColor: colors.blueAccent[700],
          },
          "& .MuiCheckbox-root": {
            color: `${colors.greenAccent[200]} !important`,
          },
          "& .MuiDataGrid-toolbarContainer .MuiButton-text": {
            color: `${colors.grey[100]} !important`,
          },
        }}
      >
        <DataGrid
          rows={sales}
          columns={columns}
          getRowId={(row)=>row.id_sale}
          initialState={{
            pagination: {
              pageSize: 25,
            },
          }}
        />
      </Box>
    </Box>
  ); 
};


export default Customer;
