import { Box, Button } from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import { useTheme } from "@mui/material";
import { useState } from "react";
import { useEffect } from "react";
import Axios from "axios";
import { useNavigate } from 'react-router-dom'
import { Link } from "react-router-dom";


import AddIcon from '@mui/icons-material/Add';
import CreateIcon from '@mui/icons-material/Create';
import { URL } from '../../data/functions';

const Sales = () => {
  const navigate = useNavigate();
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [customers, setSales] = useState([]);
  const [rowCount, setRowCount] = useState(0);
  const [curPage, setCurPage] = useState(0);
  const [rowPer, setRowPer] = useState(25);

  const searchData = (page, perPage) => {
    let url = URL+'/api/sale';
    if(page) url += `?page=${page+1}`;
    Axios({
      url,
      method: 'POST',
      data: { perPage },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setSales(data.data);
      setRowCount(data.total);
    })
  }


  useEffect(()=>{
    searchData(0, 25);
  }, [setSales, setRowCount])

  const columns = [
    { field: "id_sale", headerName: "N", flex: 0.5 },
    { field: "created_at", headerName: "Fecha", flex: 1 },
    { field: "customer_name", headerName: "Cliente", flex: 1 },
    { field: "customer_doc", headerName: "Documento", flex: 0.7, },
    { field: "customer_num", headerName: "Nmero", type: "number", headerAlign: "left", align: "left",},
    { field: "employed", headerName: "Vendedor", flex: 1,},
    { field: "total_selling", headerName: "Total Venta", flex: 1,},
    { field: "type", headerName: "Tipo de Pago", flex: 1,},
    { 
      field: "paid",
      headerName: "Estado", 
      renderCell: ({row})=>{
        return <Box>{
            (row.total_selling === row.paid)
            ?"Facturado"
            :"Pendiente"
          }
        </Box>
      }
  },
    { 
      field: "action", 
      headerName: "Accin", 
      renderCell: ({row})=>{
        return <Box>
          <Button
              variant="contained"
              size="small"
              color="primary"
              onClick={()=>navigate(`/ventas/editar/${row.id_sale}`)}
            >
            <CreateIcon/>
          </Button>
        </Box>
      }
    },
  ];



  return (
    <Box m="20px">
      <Header
        title="VENTAS"
        subtitle="Gestiona las ventas del sistema"
      />
      <Link 
        style={btnStyle}
        to={"/ventas/nuevo"}
      > <AddIcon/> NUEVO 
      </Link>
      <Box
        m="40px 0 0 0"
        height="60vh"
        sx={{
          "& .MuiDataGrid-root": {
            border: "none",
          },
          "& .MuiDataGrid-cell": {
            borderBottom: "none",
          },
          "& .name-column--cell": {
            color: colors.greenAccent[300],
          },
          "& .MuiDataGrid-columnHeaders": {
            backgroundColor: colors.blueAccent[700],
            borderBottom: "none",
          },
          "& .MuiDataGrid-virtualScroller": {
            backgroundColor: colors.primary[400],
          },
          "& .MuiDataGrid-footerContainer": {
            borderTop: "none",
            backgroundColor: colors.blueAccent[700],
          },
          "& .MuiCheckbox-root": {
            color: `${colors.greenAccent[200]} !important`,
          },
          "& .MuiDataGrid-toolbarContainer .MuiButton-text": {
            color: `${colors.grey[100]} !important`,
          },
        }}
      >
        <DataGrid
          rows={customers}
          columns={columns}
          rowCount={rowCount}
          getRowId={(row)=>row.id_sale}
          onPageChange={(page)=>{
            setCurPage(page);
            searchData(page, rowPer)
          }}
          onPageSizeChange={(pageSize)=>{
            setRowPer(pageSize);
            searchData(curPage, pageSize);
          }}
          initialState={{
            pagination: {
              pageSize: 25,
            },
          }}
        />
      </Box>
    </Box>
  ); 
};

const btnStyle = {
  color: "rgba(0, 0, 0, 0.87)",
  backgroundColor: "#4cceac",
  boxShadow: "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
  fontFamily: "Source Sans Pro,sans-serif",
  fontWeight: 500,
  fontSize: "0.75rem",
  lineHeight: 1.75,
  textTransform: "uppercase",
  minWidth: "64px",
  padding: "6px 16px",
  borderRadius: "4px",
  display: "inline-flex",
  justifyContent: "center",
  alignItems: "center",
  textDecoration: "none"
}


export default Sales;
