import { URL } from "../../data/functions";
import useMediaQuery from "@mui/material/useMediaQuery";
import { Box, Button, TextField, Snackbar, Alert } from "@mui/material";
import UserContext from '../../contents/User/UserContext';
import { useContext, useState } from "react";
import Axios from "axios";

import { useNavigate } from "react-router-dom";

const Dashboard = () => {
  const isNonMobile = useMediaQuery("(min-width:600px)");

  const navigate = useNavigate();

  const [user, setUser] = useState('');
  const [password, setPassword] = useState('')

  const [showAlert, setShowAlert] = useState(false);
  const [messageInfo, setMessageInfo] = useState({});
  
  const { setProfile } = useContext(UserContext)

  const logIn = () => {
    Axios({
      url: URL + "/api/auth/login",
      method: 'POST',
      data: {login: user, password},
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then((res)=> {
      localStorage.setItem('token', res.data.authorisation.token);
      setProfile(res.data.user)
      return navigate("/dashboard")
    }).catch(e => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

  return (
    <Box m="20px" sx={{
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center'}}
    >
      <Box sx={style}>
        <h2 id="parent-modal-title">INICIAR SESIN</h2>
        <Box
          display="grid"
          gap="30px"
          gridTemplateColumns="repeat(4, minmax(0, 1fr))"
          sx={{
            "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
          }}
        >
          <TextField
            fullWidth
            variant="filled"
            type="text"
            label="Login"
            name="login"
            value={user}
            onChange={(e) => setUser(e.target.value)}
            sx={{ gridColumn: "span 4" }}
          />
          <TextField
            fullWidth
            variant="filled"
            type="password"
            label="Contrasea"
            name="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            sx={{ gridColumn: "span 4" }}
          />
        </Box>
        <Box display="flex" justifyContent="end" mt="20px">
          <Button 
            type="submit" 
            color="secondary" 
            variant="contained"
            onClick={()=>logIn()}
          >
            INICIAR SESIN
          </Button>
        </Box>
        
      </Box>
      <Snackbar 
        open={showAlert} 
        autoHideDuration={3000}
        onClose={()=>setShowAlert(false)}
        anchorOrigin={{ vertical, horizontal }}
        key={vertical + horizontal}
        TransitionProps={{onExited:()=>{}}}
      >
        <Alert severity={messageInfo.icon} sx={{ width: '100%' }}>
          {messageInfo.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Dashboard;

const style = {
  width: 560,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const vertical = 'top';
const horizontal = "right";