import { Box, Button, TextField, Fade, Modal, FormControl, InputLabel, Select, MenuItem, Snackbar, Alert } from "@mui/material";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import { useTheme } from "@mui/material";
import { useState } from "react";
import { useEffect } from "react";
import Axios from "axios";
import { Formik } from "formik";
import * as yup from 'yup';
import useMediaQuery from "@mui/material/useMediaQuery";
import { URL } from '../../data/functions';

import { formatDate } from '../../data/functions';
import AddIcon from '@mui/icons-material/Add';
import CreateIcon from '@mui/icons-material/Create';

const Products = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [rowCount, setRowCount] = useState(0);
  const [curPage, setCurPage] = useState(0);
  const [rowPer, setRowPer] = useState(25);

  

  const [registerModal, setRegisterModal] = useState(false);
  const [updateModal, setUpdateModal] = useState(false);

  const [showAlert, setShowAlert] = useState(false);
  const [messageInfo, setMessageInfo] = useState({});
  const [updateValues, setUpdatevalues] = useState({});

  const searchData = (page, perPage) => {
    let url = URL+'/api/product';
    if(page) url += `?page=${page+1}`;
    Axios({
      url,
      method: 'POST',
      data: { perPage },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      setProducts(data.data);
      setRowCount(data.total);
    })
  }

  const getCategories = () => {
    Axios({
      url: URL+"/api/category",
      method: 'POST',
      data: { perPage: 200 },
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(({data})=> {
      initialValues.id_category = data.data[0].id;
      setCategories(data.data);
    })
  }

  const handleRegisterFormSubmit = (values) => {
    Axios({
      url: URL+"/api/product/register",
      method: 'POST',
      data: values,
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
      setMessageInfo({icon:'success', message: 'Producto registrado exitosamente'});
      setShowAlert(true);
      setRegisterModal(false);
      searchData(curPage, rowPer);
      
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

  const handleUpdateFormSubmit = ({id, image, ...values}) => {
    Axios({
      url: URL+`/api/product/update/${id}`,
      method: 'POST',
      data: values,
      headers: { "Authorization": "Bearer " + localStorage.getItem('token')}
    }).then(()=> {
      setMessageInfo({icon:'success', message: 'Producto actualizado exitosamente'});
      setShowAlert(true);
      setUpdateModal(false);
      searchData(curPage, rowPer);
    }).catch((e) => {
      if(e.response) {
        setShowAlert(true);
        setMessageInfo({icon:'error', message: e.response.data.message});
      }
      else console.log(e);
    })
  }

  useEffect(()=>{
    searchData(0, 25);
    getCategories();
  }, [setProducts, setRowCount, setCategories])

  const columns = [
    { field: "code", headerName: "Cdigo", flex: 1 },
    { field: "name", headerName: "Nombre", flex: 1 },
    { field: "measuring_unit", headerName: "UM", flex: 1 },
    { field: "category", headerName: "Categora", flex: 1 },
    { field: "stock", headerName: "Stock", flex: 1 },
    { field: "purchase_price", headerName: "P. Compra", type: "number", headerAlign: "left", align: "left",},
    { field: "selling_price", headerName: "P. Venta", type: "number", headerAlign: "left", align: "left",},
    { field: "created_at", 
      headerName: "Fecha Creacin", 
      flex: 1,
      renderCell: ({row})=>{
        return <>{formatDate(row.created_at)}</>
      }
    },
    { field: "state", headerName: "Estado", flex: 1 },

    {
      headerName: 'Accin',
      flex: 1,
      renderCell: ({row})=>{
        return <Box>
          <Button
            variant="contained"
            size="small"
            color="primary"
            onClick={()=>{
              setUpdateModal(true)
              setUpdatevalues(row);
            }}
          >
            <CreateIcon/>
          </Button>
        </Box>
      }
    }
  ];

  const isNonMobile = useMediaQuery("(min-width:600px)");


  return (
    <Box m="20px">
      <Header
        title="PRODUCTOS"
        subtitle="Gestiona los productos del almacn"
      />
      <Button 
        type="submit" 
        color="secondary" 
        variant="contained" 
        onClick={()=>setRegisterModal(true)}
      > <AddIcon/> NUEVO 
      </Button>
      <Box
        m="40px 0 0 0"
        height="75vh"
        sx={{
          "& .MuiDataGrid-root": {
            border: "none",
          },
          "& .MuiDataGrid-cell": {
            borderBottom: "none",
          },
          "& .name-column--cell": {
            color: colors.greenAccent[300],
          },
          "& .MuiDataGrid-columnHeaders": {
            backgroundColor: colors.blueAccent[700],
            borderBottom: "none",
          },
          "& .MuiDataGrid-virtualScroller": {
            backgroundColor: colors.primary[400],
          },
          "& .MuiDataGrid-footerContainer": {
            borderTop: "none",
            backgroundColor: colors.blueAccent[700],
          },
          "& .MuiCheckbox-root": {
            color: `${colors.greenAccent[200]} !important`,
          },
          "& .MuiDataGrid-toolbarContainer .MuiButton-text": {
            color: `${colors.grey[100]} !important`,
          },
        }}
      >
        <DataGrid
          rows={products}
          columns={columns}
          rowCount={rowCount}
          components={{ Toolbar: GridToolbar }}
          onPageChange={(page)=>{
            setCurPage(page);
            searchData(page, rowPer)
          }}
          onPageSizeChange={(pageSize)=>{
            setRowPer(pageSize);
            searchData(curPage, pageSize);
          }}
          initialState={{
            pagination: {
              pageSize: 25,
            },
          }}
        />
      </Box>

      <Modal
        open={registerModal}
        onClose={()=>setRegisterModal(false)}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Fade in={registerModal}>
          <Box sx={style}>
            <h2 id="parent-modal-title">Registro de Producto</h2>
            <Formik
              onSubmit={handleRegisterFormSubmit}
              initialValues={initialValues}
              validationSchema={checkoutData}
            >
              {({
                values,
                errors,
                touched,
                handleBlur,
                handleChange,
                handleSubmit
              }) => (
                <form onSubmit={handleSubmit}>
                  <Box
                    display="grid"
                    gap="30px"
                    gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                    sx={{
                      "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                    }}
                  >
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nombre"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.name}
                      name="name"
                      error={!!touched.name && !!errors.name}
                      helperText={touched.name && errors.name}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <FormControl sx={{ gridColumn: "span 2" }} variant="filled">
                      <InputLabel>Categora</InputLabel>
                      <Select
                        name="id_category"
                        value={values.id_category}
                        onChange={handleChange}
                      >
                        {
                          categories.map(category => (
                            <MenuItem 
                              key={category.id + category.name} 
                              value={category.id}
                            >{category.name}</MenuItem>
                          ))
                        }
                      </Select>
                    </FormControl>
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Unidad de Medida"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.measuring_unit}
                      name="measuring_unit"
                      error={!!touched.measuring_unit && !!errors.measuring_unit}
                      helperText={touched.measuring_unit && errors.measuring_unit}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Stock"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.stock}
                      name="stock"
                      error={!!touched.stock && !!errors.stock}
                      helperText={touched.stock && errors.stock}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Stock Mnimo"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.min_stock}
                      name="min_stock"
                      error={!!touched.min_stock && !!errors.min_stock}
                      helperText={touched.min_stock && errors.min_stock}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Descripcin"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.description}
                      name="description"
                      error={!!touched.description && !!errors.description}
                      helperText={touched.description && errors.description}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Cdigo"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.code}
                      name="code"
                      error={!!touched.code && !!errors.code}
                      helperText={touched.code && errors.code}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Precio de Compra"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.purchase_price}
                      name="purchase_price"
                      error={!!touched.purchase_price && !!errors.purchase_price}
                      helperText={touched.purchase_price && errors.purchase_price}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Precio de Venta"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.selling_price}
                      name="selling_price"
                      error={!!touched.selling_price && !!errors.selling_price}
                      helperText={touched.selling_price && errors.selling_price}
                      sx={{ gridColumn: "span 2" }}
                    />
                  </Box>
                  <Box display="flex" justifyContent="end" mt="20px">
                    <Button type="submit" color="secondary" variant="contained">
                      Registrar Producto
                    </Button>
                  </Box>
                </form>
              )}
            </Formik>
          </Box>
        </Fade>
      </Modal>

      {/* UPDATE MODAL */}
      <Modal
        open={updateModal}
        onClose={()=>setUpdateModal(false)}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Fade in={updateModal}>
          <Box sx={style}>
            <h2 id="parent-modal-title">Actualizacin de Producto</h2>
            <Formik
              onSubmit={handleUpdateFormSubmit}
              initialValues={updateValues}
              validationSchema={checkoutData}
            >
              {({
                values,
                errors,
                touched,
                handleBlur,
                handleChange,
                handleSubmit
              }) => (
                <form onSubmit={handleSubmit}>
                  <Box
                    display="grid"
                    gap="30px"
                    gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                    sx={{
                      "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                    }}
                  >
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Nombre"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.name}
                      name="name"
                      error={!!touched.name && !!errors.name}
                      helperText={touched.name && errors.name}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <FormControl sx={{ gridColumn: "span 2" }} variant="filled">
                      <InputLabel>Categora</InputLabel>
                      <Select
                        name="id_category"
                        value={values.id_category}
                        onChange={handleChange}
                      >
                        {
                          categories.map(category => (
                            <MenuItem 
                              key={category.id + category.name} 
                              value={category.id}
                            >{category.name}</MenuItem>
                          ))
                        }
                      </Select>
                    </FormControl>
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Unidad de Medida"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.measuring_unit}
                      name="measuring_unit"
                      error={!!touched.measuring_unit && !!errors.measuring_unit}
                      helperText={touched.measuring_unit && errors.measuring_unit}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Stock"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.stock}
                      name="stock"
                      error={!!touched.stock && !!errors.stock}
                      helperText={touched.stock && errors.stock}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Stock Mnimo"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.min_stock}
                      name="min_stock"
                      error={!!touched.min_stock && !!errors.min_stock}
                      helperText={touched.min_stock && errors.min_stock}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Descripcin"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.description}
                      name="description"
                      error={!!touched.description && !!errors.description}
                      helperText={touched.description && errors.description}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="text"
                      label="Cdigo"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.code}
                      name="code"
                      error={!!touched.code && !!errors.code}
                      helperText={touched.code && errors.code}
                      sx={{ gridColumn: "span 4" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Precio de Compra"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.purchase_price}
                      name="purchase_price"
                      error={!!touched.purchase_price && !!errors.purchase_price}
                      helperText={touched.purchase_price && errors.purchase_price}
                      sx={{ gridColumn: "span 2" }}
                    />
                    <TextField
                      fullWidth
                      variant="filled"
                      type="number"
                      label="Precio de Venta"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.selling_price}
                      name="selling_price"
                      error={!!touched.selling_price && !!errors.selling_price}
                      helperText={touched.selling_price && errors.selling_price}
                      sx={{ gridColumn: "span 2" }}
                    />
                  </Box>
                  <Box display="flex" justifyContent="end" mt="20px">
                    <Button type="submit" color="secondary" variant="contained">
                      Guardar
                    </Button>
                  </Box>
                </form>
              )}
            </Formik>
          </Box>
        </Fade>
      </Modal>

      {/* ALERT */}
      <Snackbar 
        open={showAlert} 
        autoHideDuration={3000}
        onClose={()=>setShowAlert(false)}
        anchorOrigin={{ vertical, horizontal }}
        key={vertical + horizontal}
        TransitionProps={{onExited:()=>{}}}
      >
        <Alert severity={messageInfo.icon} sx={{ width: '100%' }}>
          {messageInfo.message}
        </Alert>
      </Snackbar>
    </Box>
  ); 
};

const checkoutData = yup.object().shape({
  name: yup.string().required("Requerido"),
  purchase_price: yup.string().required("Requerido"),
  selling_price: yup.string().required("Requerido"),
  min_stock: yup.string().required("Requerido"),
});

const initialValues = {
  name: "",
  code: "",
  description: "",
  measuring_unit: "",
  stock: "",
  min_stock: "",
  purchase_price: "",
  selling_price: "",
  id_category: "",
  id_store: "",
};

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 560,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const vertical = 'top';
const horizontal = "right";

export default Products;
